import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { ModalShell } from '../../../../Modal';
import {
	InputField,
	InputLabelStyled,
	SelectField,
} from '../../../inputsStyles';
import { useModalsContainer } from '../../../../../context/modalsContext';
import { Checkbox, MenuItem } from '@material-ui/core';
import { functionList } from '../../../../../utils/dataMock/selectMock';
import ItemSelect from '../../../SelectField';

export const EditFunctionsModal = () => {
	const { t } = useTranslation();
	const { modalUtilizationState, closeUtilizationModal, activeContent } =
		useModalsContainer();
	const weekdays = [
		t('monday'),
		t('tuesday'),
		t('wednesday'),
		t('thursday'),
		t('friday'),
		t('saturday'),
		t('sunday'),
	];
	const dayPeriods = [t('morning'), t('afternoon'), t('night')];
	const [currentInfo, setCurrentInfo] = useState();

	useEffect(() => {
		setCurrentInfo(activeContent)
	}, [activeContent])

	const handleInput = (info, inputName) => {
		console.log(inputName)
		if(inputName == 'day') {
			const dayId = currentInfo?.Days?.find(curr => curr.day == info.day);
			console.log(dayId);
			dayId 
			? setCurrentInfo(curr => ({
				...curr,
				Days: [
					...curr.Days,
					//modify day
				],
			}))
			: setCurrentInfo((curr) => ({
				...curr,
				Days: [
					...curr.Days,
					info,
				]
			}))
		} else if(inputName == 'period') {
			const dayId = currentInfo?.Days?.filter(curr => 
				curr.day == info.day && curr.period == info.period
			)[0];
			console.log(dayId);
			// setCurrentInfo((curr) => ({
			// 	...curr,
			// 	Days: [
			// 		...curr.Days,
			// 		{
			// 			day: '',
			// 			period: info,
			// 		}
			// 	]
			// }))
		} else {
			setCurrentInfo((curr) => ({ ...curr, [inputName]: info }));
		}
	};

	const renderWeekdaysWithDayPeriod = (data) => {
		const dayId = `${data}-${Math.random()}`;
		const isChecked = !!currentInfo?.Days?.find(days => days.day == data);

		return (
			<div style={{ display: 'flex', flexDirection: 'column' }}>
				<div style={{ display: 'flex', alignItems: 'center' }}>
					<Checkbox 
						id={dayId}
						name="day"
						onChange={(e) =>
							handleInput({
								day: data,
								period: ''
							}, e.target.name)
						}
						defaultChecked={isChecked}
					/>
					<p>{t(data)}</p>
				</div>
				{dayPeriods.map((dp) => (
					<div
						style={{
							display: 'flex',
							alignItems: 'center',
						}}
					>
						<Checkbox 
							name="period"
							onChange={e => handleInput({
								day: data,
								period: t(dp)
							}, e.target.name)}
							disabled={!isChecked} 
						/>
						<p>{t(dp)}</p>
					</div>
				))}
			</div>
		);
	}

	return (
		<ModalShell
			open={modalUtilizationState}
			handleClose={closeUtilizationModal}
			title={t('addFunction')}
			breadcrumbs={['management', 'manpower']}
			actionButtons={[
				{
					onClick: () => closeUtilizationModal(),
					title: 'cancel',
					color: 'secondary',
					variant: 'outlined',
				},
				{
					onClick: () => closeUtilizationModal(),
					title: 'save',
					color: 'primary',
					variant: 'contained',
				},
			]}
		>
			<div
				style={{
					display: 'flex',
					justifyContent: 'space-between',
				}}
			>
				<div style={{ width: '48%' }}>
					<InputLabelStyled required htmlFor="functionName">
						{t('functionName')}
					</InputLabelStyled>
					<InputField
						id="functionName"
						name="functionName"
						onChange={(e) =>
							handleInput(e.target.value, e.target.name)
						}
						defaultValue={currentInfo?.Funcao}
						placeholder={t('typeSomething')}
					/>
				</div>
				<div style={{ width: '48%' }}>
					<InputLabelStyled required htmlFor="obligationsList">
						{t('obligationsList')}
					</InputLabelStyled>
					<SelectField
						id="obligationsList"
						name="obligationsList"
						onChange={(e) =>
							handleInput(e.target.value, e.target.name)
						}
						value={currentInfo?.obligationsList}
					>
						<MenuItem disabled>
							<ItemSelect value="" />
						</MenuItem>
						{functionList.map((item) => (
							<MenuItem id={item.id}>{item.name}</MenuItem>
						))}
					</SelectField>
				</div>
			</div>
			<div style={{ display: 'flex', justifyContent: 'space-between' }}>
				{weekdays.map((wd) => renderWeekdaysWithDayPeriod(wd))}
			</div>
		</ModalShell>
	);
};
