import { Button, DialogContent } from '@material-ui/core';
import {
	ModalWrapper,
	ModalHeader,
	CloseModal,
	HeaderBreadcrumb,
	HeaderTitle,
	ModalFooter,
} from './styles';
import { useTranslation } from 'react-i18next';

export const ModalShell = ({
	open,
	title,
	breadcrumbs,
	handleClose,
	actionButtons,
	children,
	...props
}) => {
	const { t } = useTranslation();

	return (
		<ModalWrapper
			{...props}
			open={open}
			onClose={handleClose}
			aria-labelledby="alert-dialog-slide-title"
			aria-describedby="alert-dialog-slide-description"
		>
			<ModalHeader>
				<div>
					<HeaderBreadcrumb>
						{breadcrumbs && breadcrumbs.map((b, i) => <p key={i}>{t(b)}</p>)}
					</HeaderBreadcrumb>
					<HeaderTitle>{t(title)}</HeaderTitle>
				</div>
				<CloseModal onClick={handleClose} />
			</ModalHeader>
			<DialogContent style={{ margin: 0, padding: 0 }}>
				{children}
				{actionButtons && (
					<ModalFooter>
						{actionButtons.map((ab, i) => (
							<Button key={i} {...ab}>{t(ab.title)}</Button>
						))}
					</ModalFooter>
				)}
			</DialogContent>
		</ModalWrapper>
	);
};
